<template>
  <view class="connect">
    <view class="connect-mode" :class="[hiddens?'connect-mode-hidden':'connect-mode-show']" @click="handerClick">
      <image :src="url" mode=""></image>
    </view>
  </view>
</template>

<script>
  export default {
    data() {
      return {
        url: require('/components/levitated/common/info.png')
      }
    },
    props: {
      hiddens: {
        type: Boolean,
        default: false
      }
    },
    methods: {
      handerClick() {

      }
    }
  }
</script>

<style scoped>
  .connect-mode {
    width: 112rpx;
    height: 116rpx;
    position: fixed;
    right: 0;
    bottom: 300rpx;
    z-index: 10;
  }

  .connect-mode image {
    display: block;
    width: 112rpx;
    height: 116rpx;
  }

  .connect-mode-hidden {
    right: 20rpx;
    animation: hiddenRight .3s linear forwards;
  }

  .connect-mode-show {
    right: 0;
    animation: showingRight .3s linear forwards;
  }

  @keyframes hiddenRight {
    0% {
      transform: translateX(0);
    }

    100% {
      transform: translateX(116rpx);
    }
  }

  @keyframes showingRight {
    0% {
      transform: translateX(0%);
    }

    100% {
      transform: translateX(-20rpx);
    }
  }
</style>